import './courses.css';
import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import notesFi from './pace';
import notesSe from './take';

const Courses = () => {
    document.title='Editable : Courses';
    const [isPending, setIsPending] = useState(true);
    const [isFinished, setIsFinished] = useState(false);
    const [firstNotes, setFirstNotes] = useState(null);
    const [secondNotes, setSecondNotes] = useState(null);
    useEffect(() => {
        if(!firstNotes) {
          getFirstNotes();
        }
        if(!secondNotes){
            getSecondNotes();
        }
      })
    
      const getFirstNotes = async () => {
        let res = await notesFi.getFirstNotes();
        setFirstNotes(res);
      }

      const getSecondNotes = async () => {
        let res = await notesSe.getSecondNotes();
      }

      const renderNotes = note => {
        return (
            <div className="note-sec">
            <div className="note-div" key={note.id} >
            <Link to={`/course/${note.title}`} className='note-link'>
                <p className='note-course'>{ note.title }</p>
            </Link>
            </div>
            </div>
        );
      };

      if(firstNotes && firstNotes.length == 0){
        setIsPending(false);
        setIsFinished(true);
      }

      if(secondNotes && secondNotes.length == 0){
          setIsPending(false);
          setIsFinished(true);
      }
    return (
        <div className="ccourses">
            <div className="courses-topbar">
                <p className="ctop">Courses</p>
            </div>
            <div className="courses-sec">
                <section className="courses-sec-first">
                    <h2 className="courses-sec-p">
                        200 lvl 1st Semester
                    </h2>
                </section>
                <div className="note-list">
                {(firstNotes && firstNotes.length > 0) ? (
          firstNotes.map(note => renderNotes(note))
        ) : (
            <>
            <p>{ isPending && <div>Loading...</div> }</p>
            <p>{isFinished && <div>No courses found</div>}</p>
            </>
        )}
                </div>
                <section className="courses-sec-second">
                    <h2 className="courses-sec-p">
                        200 lvl 2nd Semester
                    </h2>
                </section>
                <div >
                {(secondNotes && secondNotes.length > 0) ? (
          secondNotes.map(note => renderNotes(note))
        ) : (
            <>
            <p>{ isPending && <div>Loading...</div> }</p>
            <p>{isFinished && <div>No courses found</div>}</p>
            </>
        )}
                </div>
            </div>
        </div>
    )
}

export default Courses
