import './editor.css';
import { useState, useRef } from 'react';
import axios from 'axios';
import { useHistory} from "react-router-dom";
import {useOnClickOutside} from '../Navbar/close';
import {FiSend} from 'react-icons/fi';

const Editor = () => {
    const history = useHistory();
    document.title='Editable : Editor';
    const [ title, setTitle ] = useState('');
    const [ content, setContent ] = useState('');
    const [ semester, setSemester ] = useState('First-Semester');
    const [ course, setCourse ] = useState('Engineering Mechanics');
    const [open, setOpen] = useState(false);
    const toggle = () =>{
        setOpen(!open);
    }
    const handleSubmit=(event)=>{
        event.preventDefault();
        const note = {title, content, course};
        console.log(note);
        if(semester == 'First-semester'){
            axios.post('https://editable-serve.herokuapp.com/create/first-semester', note).then(()=>{
                history.push('/courses');
            })
        } else if(semester == 'Second-semester'){
            axios.post('https://editable-serve.herokuapp.com/create/second-semester', note).then(()=>{
                history.push('/courses');
            })
        }
    }

    const node = useRef(); 
    useOnClickOutside(node, () => setOpen(false));

    return (  
        <>
        <div className="everything">
            <div className="blog">
                <textarea type='text' className='title' onChange={(e) => setTitle(e.target.value)} name='title' value={title} placeholder='Course title...'></textarea>
                <textarea type='text' className='article' onChange={(e) => setContent(e.target.value)} name='title' value={content} placeholder='Start Writing here...'></textarea>
            </div>
            <div className="blog-options">
                <button className="cbtn dark cpublish-btn" onClick={toggle}><FiSend className='big'/></button>
            </div>
        </div>
            <div className="modal" ref={node}>
                <h2>Which Semester?</h2>
                <select value={semester}
                onChange={(e) => setSemester(e.target.value)}
                >
                    <option value="First-semester">First Semester</option>
                    <option value="Second-semester">Second Semester</option>
                </select>

                <h2>What course?</h2>
                <select value={course}
                onChange={(e) => setCourse(e.target.value)}
                >
                    <option value="Engineering Mechanics">Engineering Mechanics</option>
                    <option value="Engineering Mathematics">Engineering Mathematics</option>
                    <option value="Engineering Drawing">Engineering Drawing</option>
                    <option value="Basic Electrical Engineering">Basic Electrical Engineering</option>
                    <option value="Digital Logic System">Digital Logic System</option>
                    <option value="Engineering Thermodynamics">Engineering Thermodynamics</option>
                    <option value="Strength of Materials">Strength of Materials</option>
                    <option value="Science of materials">Science of materials</option>
                    <option value="Language Acquisition">Language Acquisition</option>
                    <option value="Chinese">Chinese</option>
                    <option value="French">French</option>
                </select>
                <button onClick={handleSubmit} className='signup'>
                    Add note
                </button>
            </div>
        <style jsx>{`
            .modal{
                display: ${open ? 'block' : 'none'};
            }
            .everything{
                overflow: ${open ? 'hidden' : 'visible'};
            }
        `}</style>
        </>
    );
}
 
export default Editor;