import React, {useState} from 'react';
import axios from 'axios';
import { useHistory, useParams } from "react-router-dom";

const Test = () =>{
    const history = useHistory();
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');

    const handleClick=(event)=>{
        event.preventDefault();
        const note = {title, content};
        axios.post('http://localhost:4000/create', note).then(()=>{
            alert('Note posted successfully')
            history.push('/courses');
        }).catch((Error)=>{
            alert(Error);
        })
        console.log(note);
    }

    return(
        <div className="container">
            <h1>Create Note paper</h1>
            <form action="">
                <div className="form-group">
                    <input type="text" onChange={(e) => setTitle(e.target.value)} value={title} autoComplete="off" className='form'/>
                </div>
                <div className="form-group">
                    <textarea name="" onChange={(e) => setContent(e.target.value)} name="content" value={content} autoComplete="off"></textarea>
                </div>
                <button className="btn" onClick={handleClick}>Add note</button>
            </form>
        </div>
    );
};

export default Test;