import React, {useState} from 'react';
import axios from 'axios';
import { useHistory, useParams } from "react-router-dom";
import { Link } from 'react-router-dom';
import './login.css';

const Login = () => {
    const history = useHistory();
    const [password, setPassword] = useState('');
    const [email, setEmail] = useState('');

    const handleSubmit=(event)=>{
        event.preventDefault();
        const user = {password, email};
        axios.post('https://editable-serve.herokuapp.com/login', user).then(()=>{
            history.push('/');
        }).catch((Error)=>{
            alert(Error);
        })

        console.log(user);

    }

    return (
        <div className='signup-fill'>
            <form onSubmit={handleSubmit} className='form'>
                <div className="head-center">
                    <h2><Link to='/signup' className='link h2 d'>Sign Up</Link></h2> <p className='h2 d'>/</p> <h2><Link to ='/login' className='link h2'>Login</Link></h2>
                </div>
                <input type='text' onChange={(e) => setEmail(e.target.value)}  value={email} autoComplete="off" placeholder='Email Address'></input>

                <input type='text' onChange={(e) => setPassword(e.target.value)} value={password} autoComplete="off" placeholder='Password'></input>

                <button className="btn signup">Login</button>
            </form>
        </div>
    )






}

export default Login;
