import './recent.css';
import data from './data';

const Recent = () => {
    return (  
        <div className="recent">
            <h2 className="recent-head">Recently edited</h2>
            <br />
            <div className="recent-sec">
                <ul className="recent-sec-ul">
                    <li className="recent-sec-li">
                        {data.map((data)=>{
                            if(data.course.length >= 20){
                                data.course= `${data.course.slice(0,15)}...`
                            }
                            return(
                                <div className="recent-li-sec" key={data.id}>
                                    <img src={data.img} alt="" />
                                    <section className="recent-li-down">
                                        <h3 className="recent-down-title">{data.course}</h3>
                                        <p className="recent-down-details">Written by <span className="recent-down-details-span">{data.author}</span></p>
                                    </section>
                                </div>
                            )
                        })}
                    </li>
                </ul>
            </div>
        </div>
    );
}
 
export default Recent;