import {useState, useRef } from "react";
import { Link } from "react-router-dom";
import './navbar.css';
import { useOnClickOutside } from "./close";

const Navbar = () => {
    const node = useRef(); 
    useOnClickOutside(node, () => setOpen(false));

    const [open, setOpen] = useState(false);
    const [si, setSi] = useState(false);
    const toggle = () =>{
        setOpen(!open);
    }
    const mu = () =>{
        setSi(!si);
    }

    return (  
        <>
        <div className="navbar" ref={node}>
            <div className="vi">
                <ul className="list">
                    <li className="li" ><Link to ='/' className="link" ref={node} onClick={toggle}>Home</Link></li>
                    <li className="li" ><a href='#how' className="link" ref={node} onClick={toggle}>How it works</a></li>
                    <li className="li" ><Link to ='' className="link" ref={node} onClick={toggle}>About</Link></li>
                    <li className="li accordion" onClick={mu}>Levels <span className="lef">+</span></li>
                    <li className="li a panel"><Link to ='' className="link" ref={node} onClick={toggle}>100</Link></li>
                    <li className="li a panel"><Link to ='' className="link" ref={node} onClick={toggle}>200</Link></li>
                    <li className="li a panel"><Link to ='' className="link"  ref={node} onClick={toggle}>300</Link></li>
                    <li className="li a panel"><Link to ='' className="link" ref={node} onClick={toggle}>400</Link></li>
                    <li className="li a panel"><Link to ='' className="link" ref={node} onClick={toggle}>500</Link></li>
                    <li className="li"><Link to ='/courses' className="link" ref={node} onClick={toggle} >Courses</Link></li>
                    <button className="signup"><Link to ='/login' className="link" ref={node} onClick={toggle}>Login</Link></button>
                </ul>
            </div>
            <div className="hamburger" onClick={toggle}>
                <p className="hi top"></p>
                <p className="hi m"></p>
                <p className="hi bottom"></p>
            </div>
            <h2 className="logo"><Link to ='' className="link log">Editable.</Link></h2>
        </div>
        <div className="under">
            <p className="prompt">Make your own edits for free.</p>
            <button className="signup"><Link to ='/signup' className="link">Sign up</Link></button>
        </div>
        <style jsx>{`
        .accordion{
            animation: chee ease-in-out;
            transiton: 0.5s;
        }
        .accordion{
            content: ${si ? "-" : "+"};
        }

        .a{
            display: ${si ? 'block' : 'none'}; 
            margin-bottom: ${si ? '20px' : '0'};
            transition: 0.5s;
        }
        .vi{
            display: ${open ? 'inline' : ' none'};
            transform: ${open ? 'translateX(0)' : 'translateX(-100%)'};
            transition: color 0.3s linear;
            height: ${si ? '73vh' : '52vh'};
        }
        .hamburger{
            margin-top: ${open ? '-5px' : '0px'};
            transform: ${open ? 'rotate(90deg)': 'rotate(0)'};
            width: ${open ? '17px' : '23px'};
            transition: 0.5s;
        }
        @keyframes chee {
            0%{
                height: 0;
            }
            100%{
                height: 20vh;
            }
         }
        .top{
            transform: ${open ? 'rotate(45deg)' : "rotate('0')"};
        }
        .m{
            display: ${open ? 'none' : 'block'};
            opacity: ${open ? '0' : '1'}
        }
        .bottom{
            transform: ${open ? 'rotate(-45deg)' : "rotate('0')"};
            margin-top: ${open ? '8px' : '0px'};
        }

        @media screen and (max-width: 500px) {
            .vi{
                margin-top: ${open ? '6.65vh' : '0px'};
                height: ${open ? '57vh' : '72vh' };
                height: ${si ? '79vh' : '57vh'};
            }
        }
        @media screen and (max-width: 400px) {
            .vi{
                margin-top: ${open ? '6.65vh' : '0px'};
                height: ${open ? '57vh' : '72vh' };
                height: ${si ? '79vh' : '57vh'};
            }
        }

        @media (min-width: 800px) and (max-width: 1500px){
            .vi{
                height: ${open ? '63vh' : '72vh' };
                height: ${si ? '80vh' : '59vh'};
            }
        }
        @media (min-width: 450px) and (max-width: 800px) {
            .vi{
                margin-top: ${open ? '6.65vh' : '0px'};
                height: ${open ? '59vh' : '72vh' };
                height: ${si ? '82vh' : '59vh'};
            } 
        }

        @media screen and (min-width: 400px) {
            .vi{
                margin-top: ${open ? '6.1vh' : '0px'};
                height: ${open ? '59vh' : '72vh' };
                height: ${si ? '66vh' : '47vh'};
            }
        }
        
        
        `}</style>
        </>
    );
}
 
export default Navbar;