import './courses.css';
import {useState, useEffect} from 'react';
import { useHistory, useParams } from "react-router-dom";
import notesFo from './pace';

const Course = () => {
    document.title='Editable : Course';
    const [isPending, setIsPending] = useState(true);
    const [isFinished, setIsFinished] = useState(false);
    const [notes, setNotes] = useState(null);
    const history = useHistory();
    const handleEdit = () =>{

    }
    useEffect(() => {
      if(!notes) {
        getNotes();
      }
    })
  
    const getNotes = async () => {
      let res = await notesFo.getFirstNotes();
      setNotes(res);
    }


    const handleDelete = () => {
        fetch(`/delete/${notes.title}`, {
          method: 'DELETE'
        }).then(() => {
          history.push('/');
        }) 
      }
      const renderNote = note => {
        return (
          <article className='c-article'>
          <h2 className='c-title'>{ note.title }</h2>
          <div className='c-content'>{ note.content }</div>
          <button className='signup bt' onClick={handleEdit}>Edit</button>
          </article>
        );
      };
      console.log(window.location.pathname.slice(0,8));

      if(notes && notes.length == 0){
        setIsPending(false);
        setIsFinished(true);
      }
    return (
                <div className="blog-details">
                    {/*{ error && <div>{ error }</div> }*/}
                    {(notes && notes.length > 0) ? (
                     // notes.filter(note => note.title === window.location.pathname),
          notes.map(note => renderNote(note))
        ) : (
          <>
          <p className='che'>{ isPending && <div>Loading...</div> }</p>
          <p>{isFinished && <div>No notes found</div>}</p>
          </>
        )}
                </div>
    );
}
 
export default Course;