import React, {useState, useRef } from 'react';
import axios from 'axios';
import { useHistory} from "react-router-dom";
import { Link } from 'react-router-dom';
import { useOnClickOutside } from '../Navbar/close';
import './signup.css';

const Signup = () => {
    const history = useHistory();
    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [password, setPassword] = useState('');
    const [passwordAgain, setPasswordAgain] = useState('');
    const [email, setEmail] = useState('');
    const [tick, setTick] = useState('');
    const [error, setError] = useState(false);
    const [perror, setPerror] = useState(false);
    const [paerror, setPaerror] = useState(false);
    const [paerrror, setPaerrror] = useState(false);

    const node = useRef(); 
    const pode = useRef();
    useOnClickOutside(node, () => setError(false));
    useOnClickOutside(pode, () => setPerror(false));

    const handleSubmit=(event)=>{
        event.preventDefault();
        const user = {firstName, lastName, password, passwordAgain, email};
        if(user.email || user.firstName || user.lastName || user.password || user.passwordAgain === ''){
            axios.post('https://editable-serve.herokuapp.com/signup', user).then(()=>{
            history.push('/');
            }).catch((Error)=>{
                alert(Error);
            })
            console.log(user);
        } else{
            setError(true);
        }
        if (password < 5){
            setPaerror(true);
        }

        if(password >= 12){
            setPaerrror(true);
        }

        if(password !== passwordAgain){
            setPerror(true);
        }
    }

    return (
        <div className='signup-fill'>
            <form onSubmit={handleSubmit} validate ref={node}>
            {error &&<div className="error" ref={node}>
                     <p>Please fill in the required fields</p>
                </div>}
                <div className="head-center">
                    <h2><Link to='/signup' className='link h2'>Sign Up</Link></h2> <p className='h2 d'>/</p> <h2 className="d"><Link to ='/login' className='link h2'>Login</Link></h2>
                </div>
                <div className="group">
                    <input type="text" onChange={(e) => setFirstName(e.target.value)}  value={firstName} autoComplete="off" className='form' placeholder='First Name' ref={node}/>
                    
                    <input type='text' onChange={(e) => setLastName(e.target.value)}  value={lastName} autoComplete="off" placeholder='Last Name'></input>
                </div>
                <input type='text' onChange={(e) => setEmail(e.target.value)}  value={email} autoComplete="off" placeholder='Email Address'></input>

                <input type='password' onChange={(e) => setPassword(e.target.value)} value={password} autoComplete="off" placeholder='Password'></input>
                { paerror && <p className='perror' ref={pode}>Password must longer than 5 </p> }
                { paerrror && <p className='perror' ref={pode}>Password must not be longer than 12</p> }

                <input type='password' onChange={(e) => setPasswordAgain(e.target.value)} value={passwordAgain} autoComplete="off" placeholder='Password (again)' className={perror ? 'active' : 'pa'}></input>
                { perror && <p className='perror' ref={pode}>Passwords does not match</p> }
                <section className="f">
                    <input type="checkbox" className='tick' onChange={(e) => setTick(e.target.value)} value={tick}/>
                     <p>I agree to the <Link to='/terms' className='link h2 d'>terms of use</Link></p>
                </section>

                <button className="btn signup">Sign Up</button>
            </form>
        </div>
    )
}

export default Signup;
